<template>
  <v-card>
    <v-toolbar dark color="light-blue darken-4">
      <v-btn icon dark @click.native="close">
        <v-icon>mdi-close</v-icon>
      </v-btn>
      <v-toolbar-title>Login and Start Shopping!</v-toolbar-title>
    </v-toolbar>

    <v-divider></v-divider>

    <v-container fluid>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-text-field
          v-model="email"
          :rules="emailRules"
          label="E-mail"
          required
          append-icon="mdi-email"
        >
        </v-text-field>

        <v-text-field
          v-model="password"
          :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
          :rules="passwordRules"
          :type="showPassword ? 'text' : 'password'"
          label="Password"
          hint="At least 5 charaters"
          counter
          @click:append="showPassword = !showPassword"
        >
        </v-text-field>

        <div class="text-xs-center">
          <v-btn
            color="blue darken-4 white--text"
            :disabled="!valid"
            @click="submit"
          >
            Login
            <v-icon right dark>mdi-lock-open</v-icon>
          </v-btn>
        </div>
      </v-form>
    </v-container>
  </v-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "login",
  data() {
    return {
      valid: true,
      email: "",
      emailRules: [
        (v) => !!v || "E-mail must be valid",
        (v) =>
          /([a-zA-Z0-9_]{1,})(@)([a-zA-Z0-9_]{2,}).([a-zA-Z0-9_]{2,})+/.test(
            v
          ) || "E-mail must be valid",
      ],
      showPassword: false,
      password: "",
      passwordRules: [
        (v) => !!v || "Password must be valid",
        (v) => (v && v.length >= 5) || "Password must be at least 5 characters",
      ],
    };
  },
  computed: {
    ...mapGetters({
      user: "auth/user",
      prevUrl: "prevUrl",
    }),
  },

  methods: {
    ...mapActions({
      setAlert: "alert/set",
      setAuth: "auth/set",
    }),
    submit() {
      if (this.$refs.form.validate()) {
        let formData = {
          email: this.email,
          password: this.password,
        };
        this.axios
          .post("/login", formData)
          .then((response) => {
            let { data } = response.data;
            this.setAuth(data);
            if (this.user.id > 0) {
              this.setAlert({
                status: true,
                text: "Login success",
                color: "success",
              });

              if (this.prevUrl.length > 0) {
                this.$router.push(this.prevUrl); // Tambah ini
              }

              this.close();

              this.email = "";
              this.password = "";
            } else {
              this.setAlert({
                status: true,
                color: "error",
                text: "Login failed",
              });
            }
          })
          .catch((error) => {
            let responses = error.response;
            this.setAlert({
              status: true,
              text: responses.data.message,
              color: "error",
            });
          });
      }
    },

    close() {
      this.$emit("closed", false);
    },
  },
};
</script>
